import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController } from 'ionic-angular';

@IonicPage()
@Component({
  selector: 'page-avance-examen',
  templateUrl: 'avance-examen.html',
})
export class AvanceExamenPage {

  public preguntas: any[] = this.navParams.get('preguntas')
  public answered: string[] = this.navParams.get('answered')

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController
  ) {
  }

}



